<template>
	<view class="">
		<view class="container" v-if="detailsData.title">
			<view class="header">
				<view class="header-t">
					<image :src="detailsData.titlepic" mode="" class="logo"></image>
					<view class="header-t-r">
						<view class="title">{{ detailsData.title }}</view>
						<view class="tags">
							<view class="item" v-for="item in tags" :key="item">{{ item }}</view>
						</view>
					</view>
				</view>
				<view class="header-b">{{ detailsData.smalltext }}</view>
			</view>
			<view class="info case">
				<view class="tit"><text class="tit-name">专业基本情况</text></view>
				<view class="box">
					<view class="item">现专业代码：{{ detailsData.zhuanyedaima }}</view>
					<view class="item">基本学制：{{ detailsData.jibenxuezhi }}</view>
					<view class="item">原专业代码：{{ detailsData.yzydm }}</view>
					<view class="item">原专业名称：{{ detailsData.yzymc }}</view>
					<view class="item">对口高职专业：{{ detailsData.duikougaozhi }}</view>
					<view class="item">对口本科专业：{{ detailsData.duikoubenke }}</view>
					<view class="item">培养人才目标专业：{{ detailsData.peiyangmubiao }}</view>
				</view>
			</view>
			<view class="vista case" v-if="vistaData">
				<view class="tit"><text class="tit-name">专业就业前景</text></view>
				<view class="content"><mp-html :content="vistaData"></mp-html></view>
			</view>
			<view class="introduce case" v-if="introduceData">
				<view class="tit"><text class="tit-name">专业详情介绍</text></view>
				<view class="content"><mp-html :content="introduceData"></mp-html></view>
			</view>
		</view>
		<template v-else>
			<DetailsSkeleton />
		</template>

		<!-- 相关资讯 -->
		<template v-if="relatNews.length">
			<view class="list-box">
				<NewsList
					:newsData="relatNews"
					:itemStyle="{
						'margin-top': 0,
						'border-bottom': '1px solid #eee',
					}"
				>
					<template #header>
						<DetailsTit tit="相关资讯" />
					</template>
				</NewsList>
			</view>
		</template>

		<!-- 相关专业 -->
		<template v-if="relatMajor.length">
			<view class="list-box">
				<MajorList
					:majorData="relatMajor"
					:itemStyle="{
						'margin-top': 0,
						'border-bottom': '1px solid #eee',
					}"
				>
					<template #header>
						<DetailsTit tit="相关专业" />
					</template>
				</MajorList>
			</view>
		</template>

		<DetailsSuspend :majorName="detailsData.title" />
	</view>
</template>

<script>
import DetailsSkeleton from '@/components/library/DetailsSkeleton.vue'
import { getMajorDetails, getMajorList } from '@/api/major.js'
import { getSearch } from '@/api/other.js'
import DetailsSuspend from '@/components/library/DetailsSuspend.vue'
import { websiteUrl } from '@/config/config.js'
import DetailsTit from '@/components/library/DetailsTit.vue'
import MajorList from '@/components/list/MajorList.vue'
import NewsList from '@/components/list/NewsList.vue'
import { randomsort } from '@/utils/uitls.js'
export default {
	data() {
		return {
			id: null,
			requestTask: null,
			detailsData: {},
			tags: [],
			vistaData: null,
			introduceData: null,
			relatMajor: [],
			relatNews: [],
			ttData: [
				{
					ttid: 29,
					name: '农林牧渔大类',
				},
				{
					ttid: 30,
					name: '资源环境与安全大类',
				},
				{
					ttid: 31,
					name: '能源动力与材料大类',
				},
				{
					ttid: 32,
					name: '装备制造大类',
				},
				{
					ttid: 33,
					name: '生物与化工大类',
				},
				{
					ttid: 34,
					name: '轻纺食品大类',
				},
				{
					ttid: 35,
					name: '交通运输大类',
				},
				{
					ttid: 36,
					name: '电子信息大类',
				},
				{
					ttid: 37,
					name: '医药卫生大类',
				},
				{
					ttid: 38,
					name: '财经商贸大类',
				},
				{
					ttid: 39,
					name: '旅游服务大类',
				},
				{
					ttid: 40,
					name: '文化艺术大类',
				},
				{
					ttid: 41,
					name: '新闻传播大类',
				},
				{
					ttid: 42,
					name: '教育与体育大类',
				},
				{
					ttid: 43,
					name: '公共管理与服务大类',
				},
				{
					ttid: 44,
					name: '土木水利大类',
				},
			],
		}
	},

	components: {
		DetailsSuspend,
		DetailsSkeleton,
		MajorList,
		NewsList,
		DetailsTit,
	},
	onLoad(e) {
		if (!e.id) {
			uni.switchTab({
				url: '/pages/home/home',
			})
			return
		}
		this.requestTask = getMajorDetails(e.id)
	},
	onShow() {
		if (Object.keys(this.detailsData).length === 0) {
			this.requestTask.then(({ data }) => {
				if (data.titlepic.indexOf(websiteUrl) === -1) {
					data.titlepic = websiteUrl + data.titlepic
				}
				let currTtData = this.ttData.find(v => {
					return v.ttid === Number(data.ttid)
				})
				this.tags = [
					currTtData.name,
					...data.zyshuxing.split('|').filter(item => item.length > 0),
				]
				this.vistaData = data.zhuanyeqianjing
				this.introduceData = data.newstext
				this.detailsData = data
				uni.setNavigationBarTitle({
					title: data.title,
				})
				this.setBdWebtate()
				this.getMajorListFn()
				this.getSearchNewsFn()
			})
		}
	},
	methods: {
		setBdWebtate() {
			let detailsData = this.detailsData
			// #ifdef MP-BAIDU
			swan.setPageInfo({
				title: detailsData.seotitle + ' - 上职校网',
				keywords: detailsData.keyboard,
				description: detailsData.smalltext,
				releaseDate: detailsData.newstime,
				articleTitle: detailsData.seotitle + ' - 上职校网',
				image: [websiteUrl + detailsData.titlepic],
			})
			swan.setDocumentTitle({
				title: detailsData.seotitle + ' - 上职校网',
			})
			// #endif
		},
		// 获取相关专业列表
		getMajorListFn() {
			getMajorList(this.detailsData.ttid, 1, 9).then(res => {
				let rows = res.data.rows
				rows = rows.filter(item => {
					return item.id !== this.detailsData.id
				})
				this.relatMajor = rows.sort(randomsort)
			})
		},
		getSearchNewsFn() {
			let title = this.detailsData.title
			title = title.slice(0, 5)
			getSearch(title, 1, 8).then(res => {
				this.relatNews = res.data.rows
			})
		},
	},
	// 点击分享按钮
	onShareAppMessage(res) {
		if (res.from === 'button') {
			console.log(res.target)
		}
		let pages = getCurrentPages() // 获取栈实例
		let currentRoute = pages[pages.length - 1].route // 获取当前页面路由
		let currentPage = pages[pages.length - 1]['$page'].fullPath
		return {
			title: this.detailsData.title,
			path: currentPage,
			imageUrl: this.detailsData.titlepic,
			content: this.detailsData.smalltext,
			desc: this.detailsData.smalltext,
		}
	},
}
</script>

<style lang="scss" scoped>
/deep/ page,
.swan-web-view {
	// background-color: #fff !important;
}
.container {
	background-color: #fff;
	padding: 20px 15px;
	padding-bottom: 0;
}

.header {
	padding-bottom: 15px;

	.header-t {
		display: flex;

		.logo {
			width: 64px;
			height: 64px;
			border-radius: 50%;
			margin-right: 8px;
		}

		.header-t-r {
			display: flex;
			flex-direction: column;
			justify-content: center;
			.title {
				font-weight: 600;
				font-size: 21px;
				line-height: 30px;
				color: #222;
			}

			.tags {
				.item {
					display: inline-block;
					font-size: 12px;
					margin-top: 6px;
					margin-right: 8px;
					padding: 1px 8px;
					color: #fff;
					border-radius: 20px;
					background-color: #0095fd;
				}
			}
		}
	}

	.header-b {
		margin-top: 15px;
		font-size: 16px;
		color: #555;
		text-align: justify;
		line-height: 20px;
	}
}

.case {
	margin-top: 20px;
}

.tit {
	font-size: 18px;
	font-weight: 700;
	border-bottom: 2px solid #ddd;

	.tit-name {
		border-bottom: 2px solid #0095fd;
	}
}

.info {
	.box {
		.item {
			font-size: 16px;
			color: #222;
			margin-top: 8px;
		}
	}
}

.content {
	font-size: 18px;
	color: #222;
	line-height: 29px;
	letter-spacing: 0.2px;
	text-align: justify;
}
</style>
